@import "./variables.scss";

.client-section {
    position: relative;
    overflow: hidden;
    background-color: $stark-red;
    align-items: center;
    display: flex;
    padding: 4rem 0;
    min-height: 900px;
    flex-direction: column;

    @media(min-width: $tablet) {
        padding: 2rem 1rem;
    }

    @media(min-width: $desktop) {
        padding: 8rem;
    }

    h2 {
        color: $stark-light;
        font-size: 2.5rem;
        font-weight: 700;
        text-align: center;

        @media(min-width: $tablet) {
            font-size: 3rem;
        }

        @media(min-width: $desktop) {
            font-size: 4rem;
        }
    }

    .client-block-container {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }

    .client-block-padding {
        padding: 2rem;
    }

    .client-block {
        display: flex;
        align-items: center;
        flex-direction: column;
        transition: ease all .5s;
        cursor: pointer;
        padding: 2rem;

        &.open-drawer {
            border: 3px solid $stark-dark;
            border-radius: 1rem;
            background-color: $stark-light;
            -webkit-box-shadow: 1rem 1rem 0 0 $stark-dark;
            box-shadow: 1rem 1rem 0 0 $stark-dark;

            .client-title {
                color: $stark-dark;
            }

            .client-icon {
                display: flex;
                justify-content: center;
                margin-bottom: 1rem;
                cursor: pointer;
    
                &:hover {
                    svg {
                        width: 10rem;
                        height: 10rem;
                        filter: none;
                    }
                }
            }
        }

        .client-title {
            font-size: 2rem;
            margin-bottom: 1rem;
            transition: ease all .5s;
            text-align: center;
            color: $stark-light;
            display: flex;
            align-items: center;

            @media(min-width: $tablet) {
                min-height: 9rem;
            }

            @media(min-width: $desktop) {
                min-height: 11rem;
            }
        }

        .client-icon {
            display: flex;
            justify-content: center;
            margin-bottom: 1rem;
            cursor: pointer;

            &:hover {
                svg {
                    width: 11rem;
                    height: 11rem;
                    filter: drop-shadow(3px 5px 2px rgb(0 0 0 / 0.2));
                }
            }

            svg {
                width: 10rem;
                height: 10rem;
                transition: $fast-transition;
            }
        }

        .client-description {
            transition: ease all .5s;
        }

    }
}